.settings .chosefile { margin-bottom: 20px; position: relative; height: 30px; cursor: pointer; }

.settings .chosefile input[type="file"] { opacity: 0; position: absolute; left: 0; right: 0; top: 0; bottom: 0; }

.settings .chosefile .chosfile { display: inline-block; width: 116px; text-align: center; line-height: 28px; font-size: 14px; color: #2d2f31; cursor: pointer; height: 30px; float: left; border: #dcdcdd solid 1px; -webkit-border-radius: 15px; -moz-border-radius: 15px; -ms-border-radius: 15px; border-radius: 15px; }

.settings .chosefile .filename { color: #8b8d8f; font-size: 12px; display: block; margin-left: 130px; height: 30px; line-height: 28px; white-space: nowrap; /* Запрещаем перенос строк */ overflow: hidden; /* Обрезаем все, что не помещается в область */ text-overflow: ellipsis; /* Добавляем многоточие */ }

.nav-tabs { display: block; font-size: 14px; padding-bottom: 10px; }

.nav-tabs::after { content: ""; display: table; clear: both; }

.nav-tabs li { display: block; float: left; list-style: none; width: 190px; height: 60px; background: #fff; }

.nav-tabs li a { display: block; width: 100%; height: 60px; text-align: center; line-height: 60px; color: #2d2f31; font-size: 14px; font-weight: bold; text-decoration: none; }

.nav-tabs li.green { background: #43a047; }

.nav-tabs li.green a { color: #fff; }

.nav-tabs li.yellow { background: #ffdb31; }

.nav-tabs li.blue { background: #1e88e5; }

.nav-tabs li.blue a { color: #fff; }

.nav-tabs li.active, .nav-tabs li:hover { background: #fff; }

.nav-tabs li.active a, .nav-tabs li:hover a { color: #2d2f31; }

.tab-content { font-size: 14px; line-height: 22px; }

.tab-content .tab-pane { display: none; }

.tab-content .tab-pane.active { display: block; }
