.nav-tabs {
	display: block;
	font-size: 14px;
	padding-bottom: 10px;
	@include clearfix;
	li {
		display: block;
		float: left;
		list-style: none;
		width: 190px;
		height: 60px;
		background: #fff;
		a {
			display: block;
			width: 100%;
			height: 60px;
			text-align: center;
			line-height: 60px;
			color: #2d2f31;
			font-size: 14px;
			font-weight: bold;
			text-decoration: none;
		}
		&.green {
			background: #43a047;
			a {
				color: #fff;
			}
		}
		&.yellow {
			background: #ffdb31;
		}
		&.blue {
			background: #1e88e5;
			a {
				color: #fff;
			}
		}
		&.active, &:hover {
			background: #fff;
			a {
				color: #2d2f31;
			}
		}
	}
}

.tab-content {
	font-size: 14px;
	line-height: 22px;
	.tab-pane {
		display: none;
		&.active {
			display: block;
		}
	}
}