.header {
    position: relative;
    padding: 20px 0;
    min-height: 120px;
    .logotip {
    	height: 80px;
    	position: relative;
    	a {
    		position: relative;
    		height: 80px;
    		display: block;
    	}
    	img {
    		position: absolute;
    		left: 0;
    		top: 50%;
			-moz-transform: translateY(-50%); /* Для Firefox */
			-ms-transform: translateY(-50%); /* Для IE */
			-webkit-transform: translateY(-50%); /* Для Safari, Chrome, iOS */
			-o-transform: translateY(-50%); /* Для Opera */
			transform: translateY(-50%);
    	}
    }
    .profile {
    	padding: 20px 0;
    	.warp-img {
    		width: 40px;
    		height: 40px;
    		float: left;
    		margin-right: 10px;
    		img {
    			display: block;
    			border-radius: 50%;
    		}
    	}
    	.warp-user {
    		height: 40px;
    		line-height: 40px;
    		float: left;
    		a {
    			font-size: 14px;
    			text-decoration: none;
    			color: #2d2f31;
    			&:after {
    				margin-left: 10px;
					top: -2px;
					position: relative;
    				content: "";
    				display: inline-block;
    				width: 10px;
    				height: 6px;
    				background: url(../images/dropdown.png) no-repeat;
    			}
    		}
    	}
    }
} 
.header-menu {
	padding: 20px 0;
	ul {
		padding-left: 20px;
		display: block;
		li {
			padding-right: 20px;
			display: block;
			float: left;
			list-style: none;
			a, a:visited {
				font-family:"Montserrat-Bold";
				font-size: 14px;
				font-weight: bold;
				line-height: 40px;	
				color: #2d2f31;
				text-decoration: none;
			}
		}
	}
}