// $widthContent: 960px;
$widthPageMax: none;
$widthPageMin: 0;

* {
    margin: 0;
    padding: 0;
}

html {
    height: 100%;
}

header,
nav,
section,
article,
aside,
footer {
    display: block;
}

body {
    font-style: 12px;    
    width: 100%;
    height: 100%;
    font-family:"Montserrat-Regular";
    color: #2d2f31;
}

img {
    border: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
}

div {
    margin: 0 auto;
}

a,
a:visited {
    color: blue;
    outline: none;
    text-decoration: none;
    color: #2d2f31;
}

a:hover {
    text-decoration: underline;
}

input {
    vertical-align: middle;
}


/* UTIL */

.clr {
    clear: both;
    height: 0;
    line-height: 0;
    display: block;
    float: none;
    padding: 0;
    margin: 0;
    border: none;
    font: 0px/0px;
}

.fleft {
    float: left;
}

.fright {
    float: right;
}

.tcent {
    text-align: center;
}

.tleft {
    text-align: left;
}

.tright {
    text-align: right;
}


/* headings */

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.1;
    color: #2d2f31;
}
h1 {
    font-size: 24px;
    line-height: 34px;
    color: #2d2f31;
    padding-bottom: 25px;
}
h2 {
    font-size: 20px;
    color: #2d2f31;
    padding-bottom: 25px;
}
h4 {
    font-size: 16px;
    color: #333333;
    text-align: center;
    padding-bottom: 20px;
}

.title30 {
    font-size: 30px;
    line-height: 34px;
    color: #2d2f31;
    padding-bottom: 20px;
}



/* Main */

.main {
    position: relative;
    min-width: 995px;
    min-height: 100%;
    background: #f6f6f8;
}


.content {
    position: relative;
}

.wrapper {
    padding: 0px 0px;
}

.left-col {
    float: left;
    width: 25%;
    right: 75%;
    position: relative;
}

.middle-col {
    position: relative;
    left: 25%;
    width: 75%;
    float: left;
}


.popover {
    font-family:"Montserrat-Regular";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1060;
    display: none;
    max-width: 276px;
    padding: 1px;
    font-size: 12px;
    line-height: 18px;
    font-style: normal;
    text-align: left;
    text-align: start;
    text-decoration: none;
    text-shadow: none;
    text-transform: none;
    letter-spacing: normal;
    word-break: normal;
    word-spacing: normal;
    word-wrap: normal;
    white-space: normal;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid #ccc;
    border: 1px solid rgba(0, 0, 0, .2);
    border-radius: 6px;
    -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
    box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
    color: #494b4d;
    line-break: auto;
}

.popover.top {
    margin-top: -10px;
}

.popover.right {
    margin-left: 10px;
}

.popover.bottom {
    margin-top: 10px;
}

.popover.left {
    margin-left: -10px;
}

.popover-title {
    padding: 8px 14px;
    margin: 0;
    font-size: 14px;
    font-family:"Montserrat-Bold";
}

.popover-content {
    padding: 9px 14px;
}

.popover>.arrow,
.popover>.arrow:after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
}

.popover>.arrow {
    border-width: 11px;
}

.popover>.arrow:after {
    content: "";
    border-width: 10px;
}

.popover.top>.arrow {
    bottom: -11px;
    left: 50%;
    margin-left: -11px;
    border-top-color: #999;
    border-top-color: rgba(0, 0, 0, .25);
    border-bottom-width: 0;
}

.popover.top>.arrow:after {
    bottom: 1px;
    margin-left: -10px;
    content: " ";
    border-top-color: #fff;
    border-bottom-width: 0;
}

.popover.right>.arrow {
    top: 50%;
    left: -11px;
    margin-top: -11px;
    border-right-color: #999;
    border-right-color: rgba(0, 0, 0, .25);
    border-left-width: 0;
}

.popover.right>.arrow:after {
    bottom: -10px;
    left: 1px;
    content: " ";
    border-right-color: #fff;
    border-left-width: 0;
}

.popover.bottom>.arrow {
    top: -11px;
    left: 50%;
    margin-left: -11px;
    border-top-width: 0;
    border-bottom-color: #999;
    border-bottom-color: rgba(0, 0, 0, .25);
}

.popover.bottom>.arrow:after {
    top: 1px;
    margin-left: -10px;
    content: " ";
    border-top-width: 0;
    border-bottom-color: #fff;
}

.popover.left>.arrow {
    top: 50%;
    right: -11px;
    margin-top: -11px;
    border-right-width: 0;
    border-left-color: #999;
    border-left-color: rgba(0, 0, 0, .25);
}

.popover.left>.arrow:after {
    right: 1px;
    bottom: -10px;
    content: " ";
    border-right-width: 0;
    border-left-color: #fff;
}

