.title, .panel-title {
	background-color: rgb(255, 255, 255);
	box-shadow: 0px 30px 30px 0px rgba(45, 47, 49, 0.05);
	height: 60px;
	font-size: 16px;
	font-family:"Montserrat-Bold";
	margin-bottom: 10px;
	padding-left: 30px;
	padding-right: 30px;
	line-height: 60px;
	.date-input {
		float: right;
	}
	.info {
		display: inline-block;
		background: #2d2f31;
		border-radius: 50%;
		height: 11px;
		width: 11px;
		text-align: center;
		line-height: 11px;
		font-size: 9px;
		color: #fff;
	}
	.filter {
		float: right;
		.warp-item {
			float: right;
			label {
				color: rgba(45, 47, 49, 0.702);				  	
				font-size: 14px;
				line-height: 1.286;
			}
			select {
				border: none;
				background: none;
				font-size: 14px;
				color: #2d2f31;
				-webkit-appearance: none;
				-moz-appearance: none;
				appearance: none;
				padding-right: 20px;
				background: url(../images/dropdown-select.png) no-repeat right center;
			}
		}
	}
	.searchfilter {
		margin-top: 15px;
		margin-left: 5px;
		float: right;
		width: 135px;
		height: 30px;
		border: #f8f8f8 solid 1px;
		border-radius: 15px;
		padding: 0 30px;
		background: url(../images/search.png) no-repeat 12px center;	
		input {
			width: 100%;
			background: none;
			border: none;
			float: left;
			height: 30px;			
		}
	}
}

.filterlist {
	display: block;
	float: right;
	width: 30px;
	line-height: 30px;
	text-align: center;
	height: 30px;
	margin-top: 15px;
	border: #f8f8f8 solid 1px;
	border-radius: 50%;
	color: #c4c4c4;
	position: relative;
	margin-left: 30px;
	a {
		color: #c4c4c4;
		cursor: pointer;
		text-decoration: none;
	}
	.collapse {
		-moz-transform: translate(-50%, 0); /* Для Firefox */
		-ms-transform: translate(-50%, 0); /* Для IE */
		-webkit-transform: translate(-50%, 0); /* Для Safari, Chrome, iOS */
		-o-transform: translate(-50%, 0); /* Для Opera */
		transform: translate(-50%, 0);
		position: absolute;
		top: 46px;
		left: 0;
		z-index: 10;
		padding: 20px 15px;
		display: none;
		text-align: left;
		width: 395px;
		background: #fff;
		border-radius: 5px;
		font-size: 14px;
		color: #494b4d;
		box-shadow: 0px 30px 30px 0px rgba(45, 47, 49, 0.05);
		&:after {
			bottom: 100%;
			left: 50%;
			border: solid transparent;
			content: " ";
			height: 0;
			width: 0;
			position: absolute;
			pointer-events: none;
			border-color: rgba(255, 255, 255, 0);
			border-bottom-color: #fff;
			border-width: 10px;
			margin-left: -10px;
		}
		input {
			width: 85px;
			height: 30px;
			text-align: center;
			border: #f8f8f8 solid 1px;
			border-radius: 15px;
		}
		.rowfilter {
			padding-bottom: 5px;
			&:after {
				content: "";
				display: block;
				height: 0;
				clear: both;
			}
			span {
				display: inline-block;
				width: 33px;
				height: 30px;
				line-height: 30px;
				text-align: center;
			}
			label {
				display: inline-block;
				width: 160px;
				padding-right: 10px;
				height: 30px;
				line-height: 30px;
				text-align: right;
			}
			select.sel1 {
				border: none;
				background: none;
				font-size: 14px;
				color: #2d2f31;
				border-radius: 15px;
				-webkit-appearance: none;
				-moz-appearance: none;
				appearance: none;
				border: #f8f8f8 solid 1px;
				padding-right: 20px;
				background: url(../images/dropdown-select.png) no-repeat right center;
				width: 105px;
				height: 30px;
				padding-left: 10px;
			}
			input.inp2 {
				width: 198px;
			}
			select.sel2 {
				border: none;
				background: none;
				font-size: 14px;
				color: #2d2f31;
				border-radius: 15px;
				-webkit-appearance: none;
				-moz-appearance: none;
				appearance: none;
				border: #f8f8f8 solid 1px;
				padding-right: 20px;
				background: url(../images/dropdown-select.png) no-repeat right center;
				width: 202px;
				height: 30px;
				padding-left: 10px;
			}
		}
	}
	&.active {
		.collapse {
			display: block;
		}
	}
}

.date-input {
	padding-left: 25px;
	background: url(../images/date-bg.png) no-repeat left center;
	margin-right: 30px;
	cursor: pointer;
	&:after {
		margin-left: 10px;
		top: -2px;
		position: relative;
		content: "";
		display: inline-block;
		width: 10px;
		height: 6px;
		background: url(../images/dropdown.png) no-repeat;
	}
	input {
		width: 170px;
		border: none;
		background: none;
		font-size: 14px;
		font-family:"Montserrat-Bold";
	}
}


.cifri-item {
	margin-bottom: 4px;
	.warp-top {
		height: 120px;
		margin-bottom: 20px;
		background: #2d2f31;
		position: relative;
		text-align: center;
		color: rgba(255, 255, 255, 0.702);
		font-size: 14px;
		h2 {
			color: #ffffff;
			font-size: 43px;
			font-family: 'OpenSansCondensedBold';
			display: block;
		}
	}
	.warp-but {
		height: 120px;
		color: rgba(45, 47, 49, 0.702);
		background: #fff;
		margin-bottom: 20px;
		position: relative;
		text-align: center;
		font-size: 12px;
		h2 {
			color: #2d2f31;
			font-size: 43px;
			display: block;
			font-family: 'OpenSansCondensedBold';
		}
	}
	.vartacalmidl {
		position: absolute;
		left: 50%;
		top: 50%;
		-moz-transform: translate(-50%, -50%); /* Для Firefox */
		-ms-transform: translate(-50%, -50%); /* Для IE */
		-webkit-transform: translate(-50%, -50%); /* Для Safari, Chrome, iOS */
		-o-transform: translate(-50%, -50%); /* Для Opera */
		transform: translate(-50%, -50%);
	}
	&.green {
		.warp-top {
			background: #43a047;
		}
	}
	&.blue {
		.warp-top {
			background: #1e88e5;
		}
	}
	&.red {
		.warp-top {
			background: #e53935;
		}
	}		
}

.characters {
	margin-bottom: 60px;
	.mypiechart  {
		background: #fff;
	}
}

.panel-body {
	background: #ffffff;
	margin-bottom: 60px;
	padding: 30px 30px 0;
	box-shadow: 0px 30px 30px 0px rgba(45, 47, 49, 0.05);
	background: -moz-linear-gradient(270deg, #F9F9F9 0, #FFFFFF 65%);/* FF3.6+ */
	background: -webkit-gradient(linear, 270deg, color-stop(0, F9F9F9), color-stop(65%, FFFFFF));/* Chrome,Safari4+ */
	background: -webkit-linear-gradient(270deg, #F9F9F9 0, #FFFFFF 65%);/* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(270deg, #F9F9F9 0, #FFFFFF 65%);/* Opera 11.10+ */
	background: -ms-linear-gradient(270deg, #F9F9F9 0, #FFFFFF 65%);/* IE10+ */
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#1301FE', endColorstr='#F4F60C', GradientType='1'); /* for IE */
	background: linear-gradient(270deg, #F9F9F9 0, #FFFFFF 65%);/* W3C */
	&.panel-body2 {
		padding-left: 20px;
		padding-right: 20px;
	}
	table {
		thead {
		}
		th {
			border-bottom: #dcdcdd solid 1px;
			height: 30px;
			font-size: 14px;
			color: #2d2f31;
			text-align: left;
			vertical-align: top;
			span {
				cursor: pointer;
				&:after {
					margin-left: 10px;
					top: -2px;
					position: relative;
					content: "";
					display: inline-block;
					width: 10px;
					height: 6px;
					background: url(../images/dropdown.png) no-repeat;
				}		
				&.active:after {
					-moz-transform: rotate(180deg); /* Для Firefox */
					-ms-transform: rotate(180deg); /* Для IE */
					-webkit-transform: rotate(180deg); /* Для Safari, Chrome, iOS */
					-o-transform: rotate(180deg); /* Для Opera */
					transform: rotate(180deg);
				}		
			}
		}
		td {
			padding: 20px 0;
			font-size: 12px;
			color: #494b4d;
			padding-right: 10px;
			line-height: 20px;
			vertical-align: middle;
			text-align: left;
			border-bottom: #f8f8f8 solid 1px;
			&.green {
				color: #fff;
				background: #60d465;
				a, a:visited {
					color: #fff;
				}
			}
			&.yellow {
				background: #fff265;
			}
		}
		tr:nth-last-child(1) td {
			border-bottom: #dcdcdd solid 1px;
		}
		a, a:visited {
			color: #1e88e5;
			cursor: pointer;
		}
		&.table-type-1 {
			tr {
				td:nth-child(1), th:nth-child(1) {
					width: 95px;
					text-align: left;
				}
				td:nth-child(2), th:nth-child(2) {
					width: 105px;
				}
				td:nth-child(3), th:nth-child(3) {
					width: 230px;
					text-align: left;
				}
				td:nth-child(4), th:nth-child(4) {
					width: 100px;
					text-align: left;
				}
				td:nth-child(5), th:nth-child(5) {
					width: 100px;
					text-align: left;
				}
				td:nth-child(6), th:nth-child(6) {
					width: 90px;
					text-align: left;
				}
				td:nth-child(7), th:nth-child(7) {
					width: 120px;
					text-align: left;
				}
				td:nth-child(8), th:nth-child(8) {
					width: 80px;
					text-align: left;
				}
				td:nth-child(9), th:nth-child(9) {
					width: 80px;
					text-align: left;
				}
				td:nth-child(10), th:nth-child(10) {
					width: 100px;
					text-align: left;
				}
			}		
		}
	}
	.pagenav {
		height: 60px;
		.page {
			line-height: 60px;
			font-size: 12px;
			color: #999c9e;
			float: left;
		}
		ul {
			margin-top: 15px;
			height: 30px;
			float: right;
			li {
				list-style: none;
				display: inline-block;
				margin-left: 5px;
				border: #f8f8f8 solid 1px;
				height: 30px;
				line-height: 30px;
				min-width: 30px;
				border-radius: 15px;
				text-align: center;
				&:nth-child(1), &:nth-last-child(1) {
					padding-left: 15px;
					padding-right: 15px;
				}
				a, a:visited {
					text-decoration: none;
				}
				&.active {
					border-color: #dcdcdd;
				}
				&:hover {
					border-color: #b9baba;
				}
			}
		}
	}
}

.btn {
	display: inline-block;
	width: 66px;
	height: 30px;
	cursor: pointer;
	border: #dcdcdd solid 1px;
	border-radius: 15px;
	background: none;
	&.btn-load {
		color: #ffffff;
		width: 85px;
		background: #278ee5;
		margin-left: 12px;
	}
	&.btn-save {
		color: #ffffff;
		background: #5fd364;
		margin-left: 12px;
	}
	&.btn-apply {
		color: #ffffff;
		background: #1d89e4;
		margin-left: 12px;
	}
	&.btn-large {
		color: #ffffff;
		background: #1d89e4;
		margin-left: 12px;
		width: auto;
		padding: 0 10px;
	}	
}
